import { Component, OnInit } from '@angular/core';
import { Carro } from './services/carro.object';
import { CarroService } from './services/carro.service';
import { RespuestaCarroCreacion } from './services/respuesta-carro.object';

@Component({
  selector: 'app-carro',
  templateUrl: './carro.component.html',
  styleUrls: ['./carro.component.scss']
})
export class CarroComponent implements OnInit {

  constructor(private carroService: CarroService) { }
  lstCarro: Carro[];
  carros: Carro[];
  carro: Carro;
  url: string;
  ocp: string;
  authorization: string;
  error = false;
  exito = false;
  mensaje: string;
  headers: any;
  clase = "";
  clase1 = "";
  ngOnInit(): void {
    this.inicializarCarros();
  }
  inicializarCarros() {
    this.carros = [
      {
        cilindraje: 0,
        color: '',
        linea: '',
        marca: '',
        numeroChasis: '',
        numeroMotor: '',
        placa: ''
      }
    ];
  }

  createItem() {
    this.carros.push({
      cilindraje: 0,
      color: '',
      linea: '',
      marca: '',
      numeroChasis: '',
      numeroMotor: '',
      placa: ''
    });
  }
  removeItem() {
    this.carros.splice(-1, 1)
  }

  consultarCarros() {
    if (this.url && this.ocp) {
      this.carroService.ConsultarCarros(this.url, this.ocp)
        .subscribe((res: Carro[]) => {
          this.lstCarro = res;
          this.error = false;
          this.exito = false;
        }, error => {
          this.error = true;
          this.exito = false;
          this.mensaje = "Ocurrio un error intentando realizar la consulta.";
        })
    }else{
      this.error = true;
      this.mensaje = "Diligencie los campos obligatorios";
      this.clase = "required";
      this.clase1 = "required1";
    }

  }
  guardar() {
    if (this.url && this.ocp && this.carros.length > 0) {
      this.carroService.GuardarCarros(this.url, this.ocp, this.carros, this.authorization)
        .subscribe((res: RespuestaCarroCreacion) => {
          if (res.estado) {
            this.error = false;
            this.exito = true;
            this.mensaje = res.mensaje;
          } else {
            this.error = true;
            this.exito = false;
            this.mensaje = "Ha ocurrido un error inesperado intentando consumir la api.";
          }
        }, error => {
          this.error = true;
          this.mensaje = "Ha ocurrido un error inesperado intentando consumir la api.";
        })
    } else {
      this.error = true;
      this.mensaje = "Diligencie los campos obligatorios";
      this.clase = "required";
      this.clase1 = "required1";
    }

  }

  eventoOnBlur($event){
    if($event.length > 0){
      this.clase = "";
    }else{
      this.clase = "required";
    }
  }

  eventoOnBlur1($event){
    if($event.length > 0){
      this.clase1 = "";
    }else{
      this.clase1 = "required1";
    }
  }
}
