import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { Carro } from './carro.object';

@Injectable({
  providedIn: 'root'
})
export class CarroService {

  httpHeader: any;
  constructor(readonly http: HttpClient) { }


  GuardarCarros(url: string, ocp: string, carros: Carro[], authorization?: string): Observable<Object> {
    this.httpHeader = {
      headers: new HttpHeaders({
        'Ocp-Apim-Subscription-Key': ocp,
        'Authorization': authorization
      })
    }
    return this.http.post(
      `${url}`,
      carros,
      this.httpHeader);
  }

  ConsultarCarros(url: string, ocp: string, authorization?: string): Observable<Object> {
    this.httpHeader = {
      headers: new HttpHeaders({
        'Ocp-Apim-Subscription-Key': ocp
      })
    }
    return this.http.get(
      `${url}`,
      this.httpHeader);
  }
}
